.pagination {
  display: flex;
  padding: 8px 16px;
  margin: auto 0;
  border: none;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  flex-shrink: 0, 5;
  color: var(--movieDetails-color);
}

.pagination a {
  color: var(--movieDetails-color);
  float: left;
  padding: 8px 16px;
  text-decoration: none;
  transition: background-color 0.3s;
  border-radius: 5px;
  width: 40px;
  height: 40px;
}

.pagination a.active {
  background-color: #ff6c28;
  color: var(--movieDetails-color);
}

.pagination a:hover:not(.active) {
  background-color: var(--pagination-bg);
}
.pagination-arrow {
  color: var(--movieDetails-color);
}
